class ContentArticle extends HTMLElement {
  constructor() {
    super();
    this.shadowDOM = this.attachShadow({ mode: "open" });
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.shadowDOM.innerHTML = `
            <style>
                * {
                  margin: 0;
                  padding: 0;
                  box-sizing: border-box;
                  text-decoration: none;
                }
  
                .isi {
                    padding-top: 37vw;
                    transition: 0.5s;
                  }
                  
                  .desk-kanan2 {
                    width: 100%;
                    padding-left: 3vw;
                  }
                  
                  .desk-kanan2 h1 {
                    font-weight: 500;
                    font-size: 3vw;
                  }
                  
                  .desk-kanan2 p {
                    font-weight: 300;
                    font-size: 1.5vw;
                    padding-top: 1vw;
                  }
                  
                  
            </style>
            <div class="isi">
            <article id="kelebihan" class="desk-kanan2">
              <h1>Kelebihan SkuterIn</h1>
              <p>
                SkuterIn merupakan sebuah inovasi bagi kalangan anak muda atau orang tua yang ingin eksplor suatu tempat wisata tanpa adanya rasa lelah yang berlebihan. Bagi pengunjung tempat wisata yang tidak ingin merasakan lelah maka
                sewa-lah skuter pada booth SkuterIn di sekitar lokasi tempat wisata. Kelebihan dari SkuterIn adalah praktis dan cepat dalam proses penyewaannya. Selain itu, SkuterIn memiliki harga yang terjangkau bagi semua kalangan. SkuterIn
                juga selalu mengecek kondisi dari skuter yang disewakan, sehingga penyewa akan merasa aman dan tidak khawatir mengalami kerusakan yang mendadak ditengah jalan ketika memakai sekuter kami. Oleh karena itu, jadilah orang yang
                praktis dengan menyewa skuter pada kami!
              </p>
            </article>
          </div>`;
  }
}

customElements.define("content-article", ContentArticle);
